<template>
  <div class='tags-add'>
    <el-form :model="form" :rules="rules" ref="form">
      <el-form-item label-width="80px" size="small" label="所属学科" prop="subjectID">
        <el-select v-model="form.subjectID" placeholder="请选择" style="width: 100%">
          <el-option v-for="el in list" :key="el.value" :value="el.value" :label="el.label"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label-width="80px" size="small" label="标签名称" prop="tagName">
        <el-input v-model="form.tagName" placeholder="请输入标签名称"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: ['list', 'form'],
  data() {
    return {
      rules: {
        subjectID: { required: true, message: '请选择所属学科', trigger: 'blur' },
        tagName: { required: true, message: '请输入标签名称', trigger: 'blur' }
      }
    }
  },
  mounted() {
    this.form.ref = this.$refs.form
  }
}
</script>

